<template>
  <div class="container">
    <ul class="assertInfo">
      <li class="a_item">
        <div class="left">备用金</div>
        <div class="right">500000.00￥</div>
      </li>
      <li class="a_item">
        <div class="left">预估总资产</div>
        <div class="right">
          <span>{{userData.total}}￥</span>
          <!--<span>≈</span>-->
          <!--<span>8.96423β</span>-->
        </div>
      </li>
      <li class="a_item">
        <div class="left">现金资产</div>
        <div class="right">
          <span>{{userData.entity}}￥</span>
          <span>（支付宝：{{userData.ALIPAY}}</span>
          <span>微信：{{userData.WEIXIN}}</span>
          <span>银行卡：{{userData.BANK}}）</span>
        </div>
      </li>
      <li class="a_item">
        <div class="left">币资产</div>
        <div class="right">
          <span>{{userData.fictitious}}</span>
          <span>(</span>
          <span style="margin-right: 10px" v-for="item in coinDetails">{{item.symbol}}:&nbsp;{{item.amount}}</span>
          <span>)</span>
        </div>
      </li>
    </ul>

    <div class="details">
      <div class="select">
        <div class="selectStart">
          <span>开始时间</span>
          <el-date-picker
            style="width: 180px;font-size: 12px"
            v-model="startDate"
            type="date"
            value-format="yyyy-MM-dd"
            placeholder="选择日期">
          </el-date-picker>
        </div>
        <div class="selectEnd">
          <span>结束时间</span>
          <el-date-picker
            style="width: 180px;font-size: 12px"
            v-model="endDate"
            type="date"
            value-format="yyyy-MM-dd"
            placeholder="选择日期">
          </el-date-picker>
        </div>
        <div class="selectEnd">
          <span>币种类</span>
          <el-select style="width: 150px;font-size: 10px" v-model="coin" placeholder="请选择">
            <el-option
              v-for="item in coins"
              :key="item.coin"
              :label="item.label"
              :value="item.coin">
            </el-option>
          </el-select>
        </div>
        <div @click="selectToall"  class="select_btn">查询</div>
      </div>
      <ul class="infoTip">
        <li class="infoTip_list">买币支出（cny） -120,000.00</li>
        <li class="infoTip_list">卖币收入（cny） +120,000.00</li>
        <li class="infoTip_list">手续费支出（cny） -120,000.00</li>
        <li class="infoTip_list">担保费（cny） 收 +120,000.00&nbsp;支 -120,000.00</li>
      </ul>

      <div>
        <el-table
          :data="tableData6"
          :span-method="objectSpanMethod"
          border
          style="width: 100%; margin-top: 20px">
          <el-table-column
            v-for="(item, index) in table6"
            :key="index"
            :prop="item.prop"
            align="center"
            :label="item.name">
          </el-table-column>
        </el-table>
      </div>
    </div>

    <div class="definite">
      <div style="margin-bottom: 10px">
        <span>收支明细</span>
        <span @click="deSelectFlag = !deSelectFlag" class="hand" style="color: #00C6FF;margin-left: 20px">筛选</span>
      </div>
      <div v-if="deSelectFlag" class="de_select">
        <ul class="left">
          <li class="de_item">
            <span style="margin-right: 5px">开始时间</span>
            <el-date-picker
              style="width: 160px"
              v-model="de_startDate"
              type="date"
              value-format="yyyy-MM-dd"
              placeholder="选择日期">
            </el-date-picker>
          </li>
          <li class="de_item">
            <span style="margin-right: 5px">结束时间</span>
            <el-date-picker
              style="width: 160px"
              v-model="de_endDate"
              type="date"
              value-format="yyyy-MM-dd"
              placeholder="选择日期">
            </el-date-picker>
          </li>
          <li class="de_item">
            <span style="margin-right: 5px">交易类型</span>
            <el-select style="width: 160px" v-model="dealType" placeholder="请选择">
              <el-option
                v-for="item in dealTypes"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </li>
          <li class="de_item">
            <!--<span style="margin-right: 5px">币种</span>-->
            <span style="margin-right: 5px">进账币种</span>
            <el-select style="width: 160px" v-model="coin2" placeholder="请选择">
              <el-option
                v-for="item in coin2s"
                :key="item.coin"
                :label="item.label"
                :value="item.coin">
              </el-option>
            </el-select>
          </li>
          <li class="de_item">
            <span style="margin-right: 5px">入账地址</span>
            <el-select style="width: 160px" v-model="inAddress" placeholder="请选择">
              <el-option
                v-for="item in walletAddress"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </li>
          <li class="de_item">
            <span style="margin-right: 5px">出账地址</span>
            <el-select style="width: 160px" v-model="outAddress" placeholder="请选择">
              <el-option
                v-for="item in walletAddress"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </li>
          <li class="de_item">
            <span style="margin-right: 5px">担保费</span>
            <el-select style="width: 160px" v-model="fee1" placeholder="请选择">
              <el-option
                v-for="item in fees"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </li>
          <li class="de_item">
            <span style="margin-right: 5px">手续费</span>
            <el-select style="width: 160px" v-model="fee2" placeholder="请选择">
              <el-option
                v-for="item in fees"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </li>
        </ul>
        <div class="right">
          <div @click="getDefinite" class="de_btn">查询</div>
        </div>
      </div>
      <el-table
        :data="tableData2"
        style="width: 100%"
        :row-class-name="tableRowClassName">
        <el-table-column
          prop="created_text"
          label="日期"
          width="180">
        </el-table-column>
        <el-table-column
          prop="type"
          label="交易类型">
        </el-table-column>
        <el-table-column
          prop="input_amount"
          label="入账金额">
        </el-table-column>
        <el-table-column
          prop="input_wallet_name"
          label="入账地址"
          width="100">
        </el-table-column>
        <el-table-column
          prop="output_amount"
          label="出账金额">
        </el-table-column>
        <el-table-column
          prop="output_wallet_name"
          label="出账地址">
        </el-table-column>
        <el-table-column
          prop="output_fee"
          label="出账手续费"
          width="100">
        </el-table-column>
        <el-table-column
          prop="output_esscow_fee"
          label="出账担保费" >
        </el-table-column>
        <el-table-column
          prop="input_esscow_fee"
          label="入账担保费" >
        </el-table-column>
        <el-table-column
          prop="trader_name"
          label="操作者"
          width="100">
        </el-table-column>
      </el-table>
    </div>
  </div>
</template>
<script>
  import {httpGet, httpPost, getSymbols} from '../../utils/util'
  import {mapState} from 'vuex'
  export default {
    data() {
      return {
        userData: {},
        coinDetails: [],
        startDate: '',
        endDate: '',
        coins: [],
        coin2s: [],
        coin: '',
        coin2: '',
        deSelectFlag: false,
        walletAddress: [],
        inAddress: '',
        outAddress: '',

        fees: [{
          value: '',
          label: '全部'
        }, {
          value: '1',
          label: '无'
        }, {
          value: '-1',
          label: '有'
        }],
        fee1: '',
        fee2: '',

        dealTypes: [{
          value: '',
          label: '全部'
        }, {
          value: '0',
          label: '买币'
        }, {
          value: '1',
          label: '卖币'
        }, {
          value: '2',
          label: '拆借'
        }, {
          value: '3',
          label: '还款'
        }, {
          value: '4',
          label: '代买'
        }, {
          value: '5',
          label: '代卖'
        },{
          value: '6',
          label: '申请备用金'
        },{
          value: '7',
          label: '返还备用金'
        },{
          value: '10',
          label: '自我转账',
        },{
          value: '11',
          label: '手续费'
        }],
        dealType: '',

        tableData6: [],
        table6: [
          {
            name: '货币类型',
            prop: 'symbol',
          },
          {
            name: '类型',
            prop: 'type',
          },
          {
            name: '数量',
            prop: 'amount',
          },
          {
            name: '支付现金',
            prop: 'CNY',
          },
          {
            name: '手续费（币）',
            prop: 'fee',
          },
          {
            name: '担保费(￥)',
            prop: 'esscow_fee_CNY',
          },
          {
            name: '担保费（币）',
            prop: 'esscow_fee_coin'
          }
        ],

        de_startDate: '',
        de_endDate: '',
        tableData2: []
      }
    },
    computed: {
      ...mapState(["userInfo"])
    },
    mounted() {
      this.getData();
      this.getSymbols();
      this.getAddress();

      this.selectToall();
      this.getDefinite();
    },
    methods: {
      objectSpanMethod({ row, column, rowIndex, columnIndex }) {
        if (columnIndex === 0) {
          if (rowIndex % 3 === 0) {
            return {
              rowspan: 3,
              colspan: 1
            };
          } else {
            return {
              rowspan: 0,
              colspan: 0
            };
          }
        }
      },
      tableRowClassName({row, rowIndex}) {
        if (rowIndex === 1) {
          return 'warning-row';
        } else if (rowIndex === 3) {
          return 'success-row';
        }
        return '';
      },
      //头部资产信息部分
      getData() {
        var that = this;
        httpGet("/wallet_price/profile",{
          trader_id: that.userInfo.trader_id,
          sid: that.userInfo.sid
        },function (res) {
          that.userData = res.data.all_wallet_price[0];
          that.coinDetails = res.data.all_wallet_price[0].detail;
          console.log(that.userData);
        })
      },
      getSymbols() {
        var that = this;
        httpGet("/symbols/symbols",{
          sid: that.userInfo.sid
        },function (res) {
          if(res.data.error_code == 0) {
            that.coins = res.data.symbols;
            that.coin2s = res.data.symbols;
            that.coins = that.coins.slice(1,that.coins.length-1);
            var item = {
              coin: '',
              label: '全部'
            };
            that.coins.unshift(item);
            that.coin2s.unshift(item);
          }
        })
      },
      selectToall() {
        var that = this;
        httpGet("/subtotals/esscow",{
          begin_at: that.startDate,
          end_at: that.endDate,
          symbol: that.coin,
          sid: that.userInfo.sid
        },function (res) {
          that.tableData6 = res.data.trader_subtotal;
        })
      },
    //查找交易员的钱包获取入账、出账地址
      getAddress() {
        var that = this;
        httpGet("/trader_wallets/search",{
          trader_id: that.userInfo.trader_id,
          symbol: that.coin2,
          sid: that.userInfo.sid
        },function (res) {
          if(res.data.error_code == 0) {
            var wallets = res.data.trader_wallet;
            for(var key in wallets){
              var item = {};
              item.value = wallets[key].id;
              item.label = wallets[key].wallet_name;
              that.walletAddress.push(item);
            }
            that.walletAddress.unshift({
              label: '全部',
              value: ''
            });
            console.log("address",that.walletAddress,res)
          }
        })
      },
    //查找收支明细
      getDefinite() {
        var that = this;
        httpPost("/orders/search",{
          begin_at: that.de_startDate,
          end_at: that.de_endDate,
          type: that.dealType,
          input_symbol: that.coin2,
          input_wallet_id: that.inAddress,
          output_wallet_id: that.outAddress,
          output_fee: that.fee2,
          output_esscow_fee: that.fee1,
          sid: that.userInfo.sid
        },function (res) {
          //console.log("条件查询数据",res);
          if(res.data.error_code == 0) {
            that.tableData2 = res.data.orders;
          }
        })
      }
    }
  }
</script>
<style scoped>
  .assertInfo{
    margin-bottom: 35px;
  }
  .assertInfo .a_item{
    display: flex;
  }
  .assertInfo .a_item{
    margin-bottom: 10px;
  }
  .assertInfo .a_item .left{
    width: 100px;
  }
  .assertInfo .a_item .right span{
    margin-right: 20px;
  }
  .details {
    margin-bottom: 20px;
  }
  .details .select{
    display: flex;
    align-items: center;
    margin-bottom: 20px;
  }
  .details .select .selectEnd{
    margin: 0 30px;
  }

  .details .select .select_btn{
    width: 80px;
    height: 30px;
    line-height: 30px;
    border-radius: 4px;
    background-color: #00C6FF;
    text-align: center;
    color: #fff;
    cursor: pointer;
  }

  .details .infoTip{
    display: flex;
  }

  .de_select{
    display: flex;
    width: 100%;
    height: auto;
  }
  .de_select .left{
    display: flex;
    width: 90%;
    flex-wrap: wrap;

  }
  .de_select .left .de_item{
    display: flex;
    width: 25%;
    height: 40px;
    line-height: 40px;
    margin-bottom: 20px;
  }
  .de_select .left .de_item span{
    width: 60px;
  }


  .de_select .right{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 10%;
  }
  .de_select .right .de_btn{
    width: 80px;
    height: 30px;
    line-height: 30px;
    border-radius: 4px;
    background-color: #00C6FF;
    text-align: center;
    color: #fff;
    cursor: pointer;
  }

  .el-table .warning-row {
    background: oldlace;
  }

  .el-table .success-row {
    background: #f0f9eb;
  }

  .el-table tr td{
    height: 10px;
  }

</style>



